.services-container img {
  height: 48px;
  transition: 0.4s;
}
.caption-post-section.text-center p {
  color: #ff0e67;
  font-weight: 700;
  padding: 12px 0;
}
.caption-post-section.text-center h4 {
  color: #ddd;
  font-size: 20px;
  position: relative;
  bottom: 12px;
}
.caption-post-section.text-center h6 {
  color: #fafafa;
  font-weight: 300;
  font-size: 13px;
  text-align: justify;
  line-height: 22px;
}
.card-section {
  padding: 35px 20px;
  background: #69717187;
  border-radius: 10px;
  transition: 0.4s;

  margin: 0 11px;
}
.card-section:hover .services-container img {
  height: 55px;
  width: 55px;
  background: #fff;
  padding: 8px;
  border-radius: 100%;
  transform-origin: top center;
}
.card-section:hover {
  border-radius: 10px 10px 50px 10px;
  transform: translateY(-3px);
}
