.banner {
  background: url(../../../images/cell-phone-repair.jpg) no-repeat fixed top
    center;
  background-size: cover;
  padding-bottom: 160px;
  position: relative;
}
.banner::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.685);
}
.sticky {
  background: #060606;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: block;
  transition: 0.4s;
  z-index: 1111;
}
