.Professional-section {
  background: url(../../../images/picture6.png) no-repeat top center;
  padding: 100px 0;
  background-size: cover;
  position: relative;
}

.Professional-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.808);
}
.pro-img-section img {
  position: relative;
  border-radius: 10px;
  height: 563px;
}
.topic {
  position: relative;
  padding-top: 60px;
  color: #fff;
}
.topic h3 {
  font-size: 40px;
  font-weight: 600;
  padding-top: 40px;
}
.topic h3 span {
  color: #de3213e3;
  padding: 18px 0;
}
.customer h1 {
  color: tomato;
  position: relative;
  padding-right: 132px;
  margin-top: 30px;
}
.customer p {
  position: relative;
  color: #fff;
  padding-top: 12px;
  font-weight: 400;
  font-size: 20px;
}
.topic p {
  font-size: 14px;
  line-height: 24px;
  margin-top: 29px;
}
.professional-topic {
  padding: 0 20px;
  margin-left: 170px;
}
