.testomonial-section {
  background: url(../../../images/picture9.jpg) no-repeat top center fixed;
  padding: 140px 0;
  background-size: cover;
  position: relative;
}
.testomonial-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.788);
}

button.visibility {
  opacity: 0;
  visibility: hidden;
}

.the-service-hading.text-center.py-5 h1 {
  font-size: 37px;
  margin-bottom: 45px;
  position: relative;
  color: #e6e0e0;
}
.the-service-hading.text-center.py-5 h1 span {
  color: #ff2424;
  font-weight: 700;
}
.explore-more button {
  margin-top: 35px;
  position: relative;
  border: none;
  display: inline-block;
  padding: 12px 74px;
  border-radius: 3px;
  background: linear-gradient(45deg, #ff0b0b, #475efff7);
  color: #fff;
  transition: 0.4s;
  z-index: 111;
}
