section.background {
  background: url(../../../images/picture\ 1.jpg) no-repeat fixed center center;
  background-size: cover;
  padding: 180px 0;
  position: relative;
}
section.background::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.904);
}

button.visibility {
  opacity: 0;
  visibility: hidden;
}

.the-service-hading.text-center.py-5 h1 {
  font-size: 37px;
  margin-bottom: 45px;
  position: relative;
  color: #e6e0e0;
}
.the-service-hading.text-center.py-5 h1 span {
  color: #ff2424;
  font-weight: 700;
}
.explore-more button {
  margin-top: 35px;
  position: relative;
  border: none;
  display: inline-block;
  padding: 12px 74px;
  border-radius: 3px;
  background: linear-gradient(45deg, #ff0b0b, #475efff7);
  color: #fff;
  transition: 0.4s;
  z-index: 111;
}
.explore-more button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: #da2020;
  transition: 0.4s;
  color: #fff !important;
  z-index: -1;
}
.explore-more button:hover::before {
  width: 100%;
  color: #ffff;
  border-radius: 3px;
  box-shadow: 0px -1px 19px #3586dc5b;
}
